.screen-reader-text{
  display: none;
}


.nav-previous, .nav-next {

}



.post-meta {
  font-size: 22px;
  padding: 10px;
  text-align: center;
  border: 1px solid transparent;
}

.title_meta {
  color: #333;
  font-size: 15px;
  text-align: right;
}
