body {
  width: 100%;
  font-family: "Open Sans", sans-serif;
  padding: 0;
}

body.__overflow {
  overflow: hidden;
}

a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

/**** home main ****/
.home .main {
  background-color: transparent;
  position: fixed;
  left: 0;
  right: 0;
  transition: 0.3s ease-in-out;
}

.home .main.__bg-color {
  background-color: #fff;
  transition: 0.3s ease-in-out;
}

/**** btn-round ****/
.btn-round-xs {
  width: 30px;
  height: 30px;
}

.btn-round-sm {
  width: 50px;
  height: 50px;
}

.btn-round {
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

/**** btn ****/
.btn-default {
  border-radius: 1.5rem;
}

.btn-outline {
  border: 2px solid transparent;
  border-radius: 1.5rem;
}

.btn-outline-primary {
  color: #face40;
  background-color: transparent;
  background-image: none;
  border-color: #face40;
}

.btn-outline-primary:hover, .btn-outline-primary:focus {
  color: #fff;
  background-color: #face40;
  border-color: #face40;
}

.btn-outline-white {
  color: #face40;
  background-color: #fff;
  background-image: none;
  border-color: #fff;
}

.btn-outline-white:hover, .btn-outline-white:focus {
  color: #fff;
  background-color: transparent;
  border-color: #fff;
}

.btn-default-lg {
  font-size: 1.25rem;
  padding: 0.5rem 2rem;
  border-radius: 3rem;
}

.btn .svg-inline--fa {
  margin-left: 10px;
}

.btn-dark:hover, .btn:focus {
  color: #fff;
}

.btn-dark:focus {
  box-shadow: none;
}

.btn-dark:not(:disabled):not(.disabled):active:focus {
  box-shadow: none;
}

/**** title section ****/
.title-section {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  padding-bottom: 5px;
  position: relative;
  border-left: 3px solid #face40;
  padding-left: 10px;
}

/**** description section ****/
.desc-section {
  padding: 1rem 0;
}

.main {
  width: 100%;
  height: 80px;
  background: #fff;
  position: sticky;
  top: 0;
  z-index: 11;
}

.main .container-main {
  padding-right: 35px;
  padding-left: 35px;
  margin-right: auto;
  margin-left: auto;
}

.main .container-main .branding {
  display: inline-block;
  line-height: 80px;
  position: relative;
  z-index: 1000;
  opacity: 1;
  transition: 0.3s ease-in-out;
}

.main .container-main .branding.__branding-hide {
  opacity: 0;
  z-index: 0;
  transition: 0.3s ease-in-out;
}

.main .container-main .branding .logo {
  width: 45px;
  display: inline-block;
}

.main .container-main .branding .logo svg {
  vertical-align: middle;
  transition: 0.3s ease-in-out;
  fill: #000;
}

.main .container-main .branding .title {
  color: #000;
  display: inline-block;
  padding-left: 8px;
  margin: 0;
  text-transform: capitalize;
  transition: 0.3s ease-in-out;
}

/**** icon hamb ****/
.nav-btn {
  z-index: 5000;
  position: relative;
  transition: transform 0.3s;
  width: 65px;
  height: 80px;
  float: right;
  cursor: pointer;
}

.nav-btn .nav-fill {
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  width: 30px;
  height: 2px;
  background-color: #000;
}

.nav-btn .nav-fill::before, .nav-btn .nav-fill::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 2px;
  background-color: #000;
  transform: translateY(-8px);
  transition: transform 0.3s;
}

.nav-btn .nav-fill::before {
  width: 30px;
  left: 0;
}

.nav-btn .nav-fill::after {
  width: 20px;
  left: 0;
  transform: translateY(8px);
}

.nav-btn:hover .nav-fill::before {
  transform: translateY(-10px);
}

.nav-btn:hover .nav-fill::after {
  transform: translateY(10px);
}

.nav-btn.__is-open .nav-fill {
  background: transparent !important;
}

.nav-btn.__is-open .nav-fill::before {
  width: 30px;
  left: -5px;
  background: #fff;
  transform: translateY(0) rotate(45deg);
  transition: 0.3s, transform 0.3s;
}

.nav-btn.__is-open .nav-fill::after {
  width: 30px;
  left: -5px;
  transform: translateY(0) rotate(-45deg);
  transition: 0.3s, transform 0.3s;
  background: #fff;
}

.nav-txt {
  color: #000;
  transform: rotate(-90deg);
  position: absolute;
  left: 5px;
  top: 33px;
  font-size: 12px;
}

.nav-txt.__nav-txt-white {
  color: #fff;
}

/**** menu-item ****/
.wrap-nav {
  color: #fff;
  z-index: 100;
  position: absolute;
  width: 55%;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 130px 90px 50px 90px;
  overflow: hidden;
  opacity: 0;
  transform: translateX(-100%);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: ease-in;
}

.wrap-nav.__is-open {
  opacity: 1;
  transform: translateX(0);
  transition-timing-function: cubic-bezier(0.01, 1.07, 0.54, 1.01);
  transition-delay: 0.55s;
}

.wrap-nav .menu {
  font-size: 30px;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: 900;
}

.wrap-nav .menu .menu-item {
  position: relative;
  overflow: hidden;
  margin: 10px 0;
}

.wrap-nav .menu .menu-item.current-menu-item {
  color: #face40;
}

/**** menu-item hover effect ****/
.menu-item .__color-nav {
  transition: 0.5s ease-in-out;
}

.menu-item .__color-nav:hover {
  padding-left: 15px;
  color: #face40;
  transition: 0.5s ease-in-out;
}

.menu-item .__color-nav::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 30%;
  background: #face40;
  transform: translate3d(-150%, 0, 0);
  transition: transform 0.7s, width 0.12s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  z-index: -1;
}

.menu-item .__color-nav:hover::before {
  transform: translate3d(115%, 0, 0);
  left: 0;
  width: 100%;
}

/**** main-open ****/
.shape-overlays {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  fill: #000;
}

.shape-overlays.__is-open {
  pointer-events: auto;
}

/**** info-nav ****/
.infos-nav {
  margin-top: 90px;
}

.infos-nav .title-info {
  font-family: "Open Sans", sans-serif;
  font-weight: 900;
  position: relative;
  padding-bottom: 5px;
  margin-bottom: 20px;
}

.infos-nav .title-info::before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 25px;
  height: 4px;
  background: #fff;
}

.infos-nav .info-item {
  padding: 6px 0;
}

.infos-nav .info-item span {
  padding-left: 10px;
}

.infos-nav .info-item .mail-menu {
  transition: 0.3s ease-in-out;
}

.infos-nav .info-item .mail-menu:hover {
