/**** texte metier ****/
.txt-job {
  color: #fff;
  font-size: 50px;
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  text-transform: uppercase;
  line-height: 0.8;
  margin: 0;
  padding-left: 75px;
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span {
  display: block;
}

.txt-job span:nth-child(1) {
  font-weight: 200;
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(2) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(3) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(4) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(6) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span:nth-child(7) {
  transform: translateY(0);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span.__hide {
  transform: translateY(-100vh);
  transition: transform 0.3s;
  transition-delay: 0.85s;
}

.txt-job span.__hide:nth-child(5) {
  transform: none;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  transition-delay: 0.35s;
}

.txt-job .txt-dev {
  margin-top: 40px;
}

.txt-job .txt-and {
  position: absolute;
  left: -42px;
  top: 45%;
